import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Contactform from './contactForm';

class Contact extends Component {
  render() {
    return (
      <div id="contact">
        <div className="container text-center">
          <h1>Get In Touch</h1>
          <div className="side-border">
            <FontAwesomeIcon
              icon={['fa-brands', 'fa-mixer']}
              size="lg"
              className="mt-2"
            />
          </div>
          <div className="bio">
            <div className="bio-text">
              <p className="text-muted ">
                {' '}
                I'll be so glad to be in touch with you, If you have any
                projects, or companies which need challange and you need some
                one to solve problems, just call me.
              </p>
            </div>
          </div>
          <div id="contact">
            <div className="container">
              <div className="row justify-content-center">
                <div className="col-md-4">
                  <div className="contact-info ">
                    <div className="phone-number ">
                      <FontAwesomeIcon
                        icon="fa-solid fa-mobile"
                        className="display-4 mb-3"
                      />
                      <h5>Call Me On</h5>
                      <p className="text-muted">(+98)9300236072</p>
                    </div>
                    <div className="email mt-4">
                      <FontAwesomeIcon
                        icon="fa-solid fa-envelope"
                        className="display-4 mb-3"
                      />
                      <h5>Email Me at</h5>
                      <p className="text-muted">amirhossein1998.pz@gmail.com</p>
                    </div>
                  </div>
                </div>
                <div className="col-md-8">
                  <Contactform />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;
