import React, { Component } from 'react';
import Joi from 'joi-browser';
import Form from './form';
import Input from './input';

class Contactform extends Form {
  state = {
    form: { name: '', email: '', subject: '', message: '' },
    errors: {},
  };

  schema = {
    name: Joi.string().alphanum().min(3).max(30).required(),
    email: Joi.string().email({
      minDomainSegments: 2,
      tlds: { allow: ['com', 'net'] },
    }),
    subject: Joi.string().alphanum().min(3).max(30).required(),
    message: Joi.string().alphanum().min(10).required(),
  };

  render() {
    const { form, errors } = this.state;
    return (
      <div className="contact-form">
        <form>
          <div className="row">
            <div className="col-md-6 form-group text-left">
              <Input
                formType="input"
                name="name"
                label="Name"
                onChange={this.handleChange}
                value={form.name}
                errors={errors.name}
              />
            </div>
            <div className="col-md-6 form-group text-left">
              <Input
                formType="input"
                name="email"
                label="Email"
                onChange={this.handleChange}
                value={form.email}
                errors={errors.email}
              />
            </div>
          </div>
          <div className=" form-group text-left">
            <Input
              formType="input"
              name="subject"
              label="Subject"
              onChange={this.handleChange}
              value={form.subject}
              errors={errors.subject}
            />
          </div>
          <div className="form-group text-left">
            <Input
              formType="textArea"
              name="message"
              label="Message"
              onChange={this.handleChange}
              value={form.message}
              errors={errors.message}
              cols="10"
              rows="6"
            />
          </div>
          <button
            onClick={this.handleSubmit}
            disabled={this.validate()}
            type="submit"
            className="btn btn-outline-dark btn-block mt-2"
          >
            Send Message
          </button>
        </form>
      </div>
    );
  }
}

export default Contactform;
