import React, { Component, Fragment } from 'react';

const Input = ({
  name,
  label,
  onChange,
  value,
  errors,
  formType,
  cols,
  rows,
}) => {
  if (formType === 'input') {
    return (
      <Fragment>
        <label htmlFor={name}>{label}</label>
        <input
          onChange={onChange}
          value={value}
          type="text"
          className="form-control"
          id={name}
          name={name}
          placeholder={`Your ${name}`}
        ></input>
        {errors && <div className="alert alert-danger mt-2">{errors}</div>}
      </Fragment>
    );
  }
  return (
    <Fragment>
      <label htmlFor={name}>{label}</label>
      <textarea
        onChange={onChange}
        value={value}
        type="text"
        className="form-control"
        id={name}
        name={name}
        placeholder={`Your ${name}`}
        cols={cols}
        rows={rows}
      ></textarea>
      {errors && <div className="alert alert-danger mt-2">{errors}</div>}
    </Fragment>
  );
};

export default Input;
