import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class Footer extends Component {
  render() {
    return (
      <div id="footer" className="bg-light">
        <div className="container">
          <div className="bg-light row justify-content-between text-center ">
            <p className="text-muted col-md-4">2021 &copy; Designed By Amir</p>
            <div id="social" className="col-md-4 text-center">
              <a
                target="_blank"
                href="https://t.me/Amir_pezeshkpourr"
                className="text-white"
              >
                <FontAwesomeIcon
                  icon={['fab', 'telegram']}
                  size="lg"
                  className="footer-icon text-muted"
                />
              </a>
              <a
                target="_blank"
                href="https://wa.me/989300236072"
                className="text-white"
              >
                <FontAwesomeIcon
                  icon={['fab', 'whatsapp']}
                  size="lg"
                  className="footer-icon text-muted"
                />
              </a>
              <a
                target="_blank"
                href="https://github.com/Amirhossein-pz"
                className="text-white"
              >
                <FontAwesomeIcon
                  icon={['fab', 'github']}
                  size="lg"
                  className="footer-icon text-muted"
                />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Footer;
