import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class About extends Component {
  render() {
    return (
      <div id="about">
        <div className="container text-center">
          <h1>About Me</h1>
          <div className="side-border">
            <FontAwesomeIcon
              icon={['fa-brands', 'fa-mixer']}
              size="lg"
              className="mt-2"
            />
          </div>
          <div className="bio">
            <div className="bio-text">
              <p className="text-muted ">
                {' '}
                My name is Amirhossein Pezeshkpour, I enjoyed working with
                computer since I was a litle boy I also loved solving diffrent
                problems so it makes sence why I'm learning and working as a
                Front-end developer!
              </p>
            </div>
          </div>
          <div id="bio-img my-4" className="d-flex justify-content-center">
            <img
              src={require('../img/about-me.jpg')}
              alt=""
              className="img-fluid rounded-circle w-25"
            />
          </div>
          <p className="skills-title text-muted">UI | UX | Front-End</p>
          <div className="bio">
            <p className="bio-text text-muted">
              I started leaning coding since 2019, but from 2020 I take it more
              serious and I choose web development and front-end to work on.My
              english is at good level I took my academic Ielts exam at 2020
              with the score 6.5, So I can handle international projects too.
            </p>
          </div>
          <div className="skill-progress-bar my-4">
            <div className="container">
              <div className="row ">
                <div className="col-md-6 text-left">
                  <div className="skill-item skill-item-html">
                    <div className="skill-item-content d-flex flex-row-reverse justify-content-between">
                      <div className="skill-item-percentage">90%</div>
                      <div className="skill-item-title">
                        <h5>HTML</h5>
                      </div>
                    </div>
                  </div>
                  <div className="skill-item skill-item-css">
                    <div className="skill-item-content d-flex flex-row-reverse justify-content-between">
                      <div className="skill-item-percentage">90%</div>
                      <div className="skill-item-title">
                        <h5>CSS</h5>
                      </div>
                    </div>
                  </div>
                  <div className="skill-item skill-item-js">
                    <div className="skill-item-content d-flex flex-row-reverse justify-content-between">
                      <div className="skill-item-percentage">50%</div>
                      <div className="skill-item-title">
                        <h5>JavaScript</h5>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-md-6 text-left">
                  <div className="skill-item skill-item-bootstrap">
                    <div className="skill-item-content d-flex flex-row-reverse justify-content-between">
                      <div className="skill-item-percentage">90%</div>
                      <div className="skill-item-title">
                        <h5>Bootstrap</h5>
                      </div>
                    </div>
                  </div>
                  <div className="skill-item skill-item-react">
                    <div className="skill-item-content d-flex flex-row-reverse justify-content-between">
                      <div className="skill-item-percentage">40%</div>
                      <div className="skill-item-title">
                        <h5>React</h5>
                      </div>
                    </div>
                  </div>
                  <div className="skill-item skill-item-english">
                    <div className="skill-item-content d-flex flex-row-reverse justify-content-between">
                      <div className="skill-item-percentage">60%</div>
                      <div className="skill-item-title">
                        <h5>English</h5>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
