import Typewriter from 'typewriter-effect';

function TypeEffect() {
  return (
    <div className="type-effect">
      <Typewriter
        onInit={(typewriter) => {
          typewriter.pauseFor(1000).deleteAll().start();
        }}
        options={{
          strings: ['I Am Amir.', ' I Am Developer.'],
          autoStart: true,
          loop: true,
        }}
      />
    </div>
  );
}

export default TypeEffect;
